body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

div.filter-button {
  flex-grow: 1;
  text-align: center;
  margin-left: 20px;
  margin-right: 20px;
}
.searchbar-container {
  width: 40%;
  margin-left: 30%;
  margin-right: 30%;
  padding-top: 30px;
  padding-bottom: 20px;
}

.searchbar-input {
  width: 85%;
}

.searchbar-button {
  width: 15%;
}
.Home-Page {
  width: 100%;
}

.filter-wrapper {
  width: 50%;
  margin-left: 25%;
  margin-right: 25%;
  display: flex;
}

.Result-Table {
  width: 80%;
  margin-left: 10%;
  margin-top: 25px;
}

.table {
  min-width: 1020px;
}

.table-wrapper {
  overflow: auto;
  margin: 10px;
}
.genre-item {
  width: 18%;
  margin-top: -5px;
  margin-bottom: -5px;
  text-align: left;
}
.genre-wrapper {
  width: 60%;
  margin-left: 20%;
  margin-right: 20%;
}

.App {
  text-align: center;
}

.App-logo {
  height: 80px;
  margin-top: 10px;
}
.Header-App-Bar {
  flex-grow: 1;
}

.grow {
  flex-grow: 1;
}

.menu-button {
  margin-left: 12px;
  margin-right: 20px;
}
